.wp-animate{
  -webkit-animation-name: inUpAnimate, rotate360;
          animation-name: inUpAnimate, rotate360;
  -webkit-animation-duration: 2000ms, 2000ms;
          animation-duration: 2000ms, 2000ms;
  -webkit-animation-delay: 0ms, 2000ms;
          animation-delay: 0ms, 2000ms; /* add this */
  -webkit-animation-timing-function: ease-in, ease-in-out;
          animation-timing-function: ease-in, ease-in-out;
  -webkit-animation-iteration-count: 1, infinite;
          animation-iteration-count: 1, infinite;
}
@-webkit-keyframes rotate360 {
  0%   {transform: translateX(5px);}
  10%  {transform: translateX(-5px);}
  20% {transform: translateX(5px);}
  30%   {transform: translateX(-5px);}
  40%  {transform: translateX(5px);}
  50% {transform: translateX(-5px);}
  60% {transform: translateX(0);}
  100% {transform: translateX(0);}
}
@keyframes rotate360 {
  0%   {transform: translateX(5px);}
  10%  {transform: translateX(-5px);}
  20% {transform: translateX(5px);}
  30%   {transform: translateX(-5px);}
  40%  {transform: translateX(5px);}
  50% {transform: translateX(-5px);}
  60% {transform: translateX(0);}
  100% {transform: translateX(0);}
}

.laravel-animate{
  -webkit-animation-name: inUpAnimate, rotate;
          animation-name: inUpAnimate, rotate;
  -webkit-animation-duration: 2000ms, 8s;
          animation-duration: 2000ms, 8s;
  -webkit-animation-delay: 200ms, 2200ms;
          animation-delay: 200ms, 2200ms; /* add this */
  -webkit-animation-timing-function: ease-in, ease-in-out;
          animation-timing-function: ease-in, ease-in-out;
  -webkit-animation-iteration-count: 1, infinite;
          animation-iteration-count: 1, infinite;
}
@-webkit-keyframes rotate {
  0%   {transform: rotateX(180deg) rotateY(360deg);}
  20%  {transform: rotateX(-50deg) rotateY(-50deg);}
  30% {transform: rotateX(180deg) rotateY(360deg);}
  100%   {transform: rotateX(180deg) rotateY(360deg);}
}
@keyframes rotate {
  0%   {transform: rotateX(180deg) rotateY(360deg);}
  20%  {transform: rotateX(-50deg) rotateY(-50deg);}
  30% {transform: rotateX(180deg) rotateY(360deg);}
  100%   {transform: rotateX(180deg) rotateY(360deg);}
}

