/* Some positioning and ratios */
.sticky-container {
  position: relative;
}

.sticky-outer {
  display: flex;
  /*padding-top: 92.5925926%;*/
  position: relative;

  width: 100%;
}

.sticky {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

/* Shadow behind the sticky note */
.sticky:before {
  box-shadow: -2px 2px 15px 0 rgba(0, 0, 0, 0.5);
  background-color: rgba(0, 0, 0, 0.25);
  content: '';
  width: 90%;
  left: 5px;
  height: 75%;
  position: absolute;
  top: 30%;

  -webkit-animation-name: sticky-shadow-animation;

          animation-name: sticky-shadow-animation;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-delay: 0ms;
          animation-delay: 0ms; /* add this */
  /*animation-timing-function: ease-in;*/
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes sticky-shadow-animation {
  0% {
    top : 30%;
    box-shadow: -2px 2px 15px 0 rgba(0, 0, 0, 0.5);
  }
  50% {
    top : 29%;
    box-shadow: -2px 2px 16px 0 rgba(0, 0, 0, 0.5);
  }
  100% {
    top : 30%;
    box-shadow: -2px 2px 15px 0 rgba(0, 0, 0, 0.5);
  }
}

@keyframes sticky-shadow-animation {
  0% {
    top : 30%;
    box-shadow: -2px 2px 15px 0 rgba(0, 0, 0, 0.5);
  }
  50% {
    top : 29%;
    box-shadow: -2px 2px 16px 0 rgba(0, 0, 0, 0.5);
  }
  100% {
    top : 30%;
    box-shadow: -2px 2px 15px 0 rgba(0, 0, 0, 0.5);
  }
}

