/* Position the sticky nicely, so it looks better */
html,
body {
  height: 100%;
  margin: 0;
  font-size: 16px;
}

.container {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.container-inner {
  /*width: 50%;*/
  /*margin: 25px;*/
  background-color: red;
}

/* Add responsiveness */
@media screen and (min-width: 640px) {
  .sticky-content {
    font-size: 1.5rem;
  }
  .container-inner {
    width: 50%;
  }
}

@media screen and (min-width: 768px) {
  .sticky-content {
    font-size: 1.5rem;
  }
  .container-inner {
    width: 50%;
  }
}

@media screen and (min-width: 1024px) {
  .sticky-content {
    font-size: 1.875rem;
  }
  .container-inner {
    width: 25%;
  }
}
